.friends-tab-container {
  display: flex;
  width: 100%;
  height: calc(100vh - var(--nav-size));
}


.friends-tab-menu {
  width: 360px;
  height: calc(100vh - var(--nav-size));
  position: relative;

  background-color: var(--primary);

  border-right: 1px solid rgba(0, 0, 0, 0.13);
  box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.07);

  overflow: hidden;
}



.friends-tab-menu-slide-left-enter {
  position: absolute;
  transform: translateX(-110%);
}

.friends-tab-menu-slide-left-enter-active {
  transform: translateX(0%);
  transition: transform var(--fast-speed) ease;
}

.friends-tab-menu-slide-left-exit {
  position: absolute;
}

.friends-tab-menu-slide-left-exit-active {
  transform: translateX(-110%);
  transition: transform var(--fast-speed) ease;
}



.friends-tab-menu-slide-right-enter {
  position: absolute;
  transform: translateX(110%);
}

.friends-tab-menu-slide-right-enter-active {
  transform: translateX(0%);
  transition: transform var(--fast-speed) ease;
}

.friends-tab-menu-slide-right-exit {
  position: absolute;
}

.friends-tab-menu-slide-right-exit-active {
  transform: translateX(110%);
  transition: transform var(--fast-speed) ease;
}








.friends-tab-menu-page {
  width: 100%;
}

.friends-tab-menu-item {
  height: 48px;
  min-width: auto;
  
  display: flex;
  align-items: center;

  padding: 2px;
  margin-left: 6px;
  margin-right: 6px;

  white-space: nowrap;
  cursor: pointer;
  user-select: none;
  text-decoration: none;
  color: var(--primary-text);

  border-radius: var(--border-radius);

  transition-delay: 25ms;
  transition: background-color var(--fast-speed) ease;
}

.friends-tab-menu-item:hover {
  transition: none;
  background-color: var(--primary-highlight);
}

.friends-tab-menu-item span {
  padding-left: 0.75rem;

  font-size: 1.0625rem;
  font-weight: 500;
}

.friends-tab-menu-icon-button {
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 8px;
  margin-left: 4px;

  border-radius: 50%;
  background-color: var(--secondary-button-background);
}

.friends-tab-menu-icon-button i {

}


.friends-tab-menu-icon-right {
  background-color: transparent;
  margin-left: auto;
  margin-right: 4px;
  padding: 0px;
}

.friends-tab-menu-icon-right svg {
  fill: var(--button-lighter-color);
  padding: 0px;
  width: 18px;
  height: 18px;
}






.friends-tab-menu-item-activated {
  background-color: var(--background-deemphasized);
}

.friends-tab-menu-item-activated .friends-tab-menu-icon-button {
  background-color: var(--accent);
}

.friends-tab-menu-item-activated .friends-tab-menu-icon-button i {
  filter: invert(1.0);
}










.friends-tab-menu-info {
  display: flex;
  align-items: center;

  margin-top: 10px;
  margin-bottom: 12px;
  margin-left: 16px;
  margin-right: 16px;
}

.friends-tab-menu-settings {
  display: flex;
  align-items: center;
  
  margin-left: auto;
  padding: 8px;
  
  cursor: pointer;

  border-radius: 50%;
  background-color: var(--secondary-button-background);
  transition: filter var(--fast-speed) ease;
  filter: brightness(1.0);
}

.friends-tab-menu-settings:hover {
  filter: brightness(0.95);
}

.friends-tab-menu-settings svg {
  width: 20px;
  height: 20px;
}

.friends-tab-menu-title {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.75rem;
  font-family: var(--segoeui);

  color: var(--primary-text);
}






.friends-tab-menu-page-navigation {
  display: flex;
  align-items: center;

  margin-top: 16px;
  margin-left: 12px;
  margin-bottom: 12px;
}

.friends-tab-menu-page-navigation a {
  display: flex;
}

.friends-tab-menu-page-navigation svg {
  width: 16px;
  height: 16px;

  padding: 8px;
  border-radius: 50%;

  cursor: pointer;
  user-select: none;

  color: var(--secondary-text);
  transition: background-color var(--fast-speed) ease;
}

.friends-tab-menu-page-navigation svg:hover {
  background-color: var(--primary-highlight);
}

.friends-tab-menu-page-title-container {
  display: flex;
  flex-direction: column;

  margin-left: 12px;
}

.friends-tab-menu-page-link {
  width: fit-content;

  font-size: .8125rem;
  font-weight: 400;
  color: var(--secondary-text);
  text-decoration: none;
  
  cursor: pointer;
  user-select: none;
}

.friends-tab-menu-page-link:hover {
  text-decoration: underline;
}

.friends-tab-menu-page-title {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.75rem;
  font-family: var(--segoeui);
}






.friends-tab-menu-search {
  margin-left: 16px;
  margin-right: 16px;
  margin-bottom: 16px;
  padding: 9px 8px 8px 8px;

  display: flex;
  align-items: center;
  overflow: hidden;

  border: 0px;
  border-radius: 20px;

  background-color: var(--comment-background);
  line-height: 0.9375rem;

  transition: width var(--fast-speed) ease, margin-left var(--fast-speed) ease;
}


.friends-tab-menu-search-icon {
  margin-top: 1px;
  margin-left: 4px;

  transition: margin-left var(--fast-speed) ease;
}

.friends-tab-menu-search-icon svg {
  color: var(--secondary-icon);

  width: 16px;
  height: 16px;

  transition: width var(--fast-speed) ease, height var(--fast-speed) ease, filter var(--fast-speed) ease, margin-top var(--fast-speed) ease;
}

.friends-tab-menu-search-input {
  height: 100%;
  width: 100%;

  padding-left: 1ch;
  margin-top: -3px;

  font-family: "Segoe UI Historic", "Segoe UI", Helvetica, Arial, sans-serif;
  font-size: 0.9375rem;
  font-weight: 400;
  color: black;

  background-color: transparent;
  border: 0px;
}

.friends-tab-menu-search-input:focus {
  outline: none;
}

