.menu-item {
  height: 48px;
  width: auto;
  
  white-space: nowrap;
  cursor: pointer;

  display: flex;
  align-items: center;

  border-radius: var(--border-radius);
  padding: 7px;
  margin-left: 6px;
  margin-right: 6px;
}

.menu-item span {
  padding-left: 0.75rem;
}

.menu-icon-button {
  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 20px;
  
  background-color: var(--secondary-button-background);
}

.menu-icon-button svg {
  fill: var(--button-color);

  --button-size: calc(var(--nav-button-size) * 0.5);
  width: var(--button-size);
  height: var(--button-size);
  
  padding: 10px;
}

.menu-item:hover {
  background-color: var(--primary-highlight);
}

.menu-icon-right {
  background-color: transparent;
  margin-left: auto;
}

.menu-icon-right svg {
  fill: var(--button-lighter-color);
  padding: 0px;
  width: 24px;
  height: 24px;
}





.options-item {
  white-space: normal;

  height: auto;
  width: auto;

  display: flex;
  align-items: flex-start;

  border-radius: var(--border-radius);
  padding: 7px;
  margin-left: 6px;
  margin-right: 6px;
}

.options-item-info {
  
}

.options-item-title {
  padding-left: 0.75rem;

  font-size: 1.1rem;
  font-weight: 600;
}

.options-item-description {
  padding-left: 0.75rem;
  padding-bottom: 0.5rem;

  font-size: 0.9rem;
  font-weight: 400;
  color: var(--secondary-text);
}

.options-item-option-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;

  padding-left: 0.25rem;
}

.options-item-option {
  width: 100%;
  padding-left: 0.5rem;
  padding-top: 0.8rem;
  padding-bottom: 0.8rem;

  display: flex;
  align-items: center;

  border-radius: var(--border-radius);
  cursor: pointer;

  transition: background-color 100ms ease;
}

.options-item-option:hover {
  background-color: var(--primary-highlight);
}

.options-item-option input {
  margin-left: auto;
  margin-right: 0.75rem;

  appearance: none;
  background-color: #FFFFFF;
  width: 1.24em;
  height: 1.25em;
  border: 0.3em solid #FFFFFF;
  border-radius: 50%;

  box-shadow: 0 0 0 0.15em rgb(28, 30, 33);

  transform: translateY(-0.05em);
}

.options-item-option input:checked {
  margin-left: auto;
  margin-right: 0.75rem;

  appearance: none;
  background-color: var(--accent);
  width: 1.24em;
  height: 1.25em;
  border: 0.3em solid #FFFFFF;
  border-radius: 50%;

  box-shadow: 0 0 0 0.15em var(--accent);

  transform: translateY(-0.05em);
}





.menu-mini-profile {
  width: auto;
  height: fit-content;
}

.menu-mini-profile img {
  width: 60px;
  height: 60px;

  padding: 2px;
  border-radius: 100%;
}

.menu-mini-profile div {
  margin-right: auto;
}



.menu-title-item {
  cursor: auto;
}

.menu-title-item span {
  font-size: 1.5rem;
  font-weight: 700;

  padding-left: 1rem;
  padding-bottom: 5px;
}

.menu-title-item:hover {
  background-color: transparent;
}

.menu-title-item .menu-icon-button {
  transition: background-color var(--fast-speed) ease;
  background-color: var(--primary);
}

.menu-title-item .menu-icon-button:hover {
  background-color: var(--primary-highlight);
  cursor: pointer;
}

