.content-reactions-container {
  display: flex;
  align-items: center;

  margin-top: 8px;
  margin-bottom: 12px;
}

.content-reactions-icon {
  width: 18px;
  height: 18px;
  margin-left: 16px;

  cursor: pointer;
}

.content-reactions-icon svg {
  width: 100%;
  height: 100%;
}

.content-reactions-number {
  margin-left: 6px;

  user-select: none;
  cursor: pointer;

  font-size: .9375rem;
  color: var(--secondary-text);
}

.content-reactions-number:hover {
  text-decoration: underline;
}

.content-reactions-comments-number {
  margin-right: 16px;
  margin-left: auto;
  
  user-select: none;
  cursor: pointer;

  font-size: .9375rem;
  color: var(--secondary-text);
}

.content-reactions-comments-number:hover {
  text-decoration: underline;
}

.content-reactions-shares-number {
  margin-right: 16px;
  /* margin-left: auto; */
  
  user-select: none;
  cursor: pointer;

  font-size: .9375rem;
  color: var(--secondary-text);
}

.content-reactions-shares-number:hover {
  text-decoration: underline;
}




.content-reactions-menu {
  display: flex;
  align-items: center;

  margin-left: 16px;
  margin-right: 16px;
}

.content-reactions-button {
  height: 32px;
  
  margin: 2px;
  margin-top: 3px;
  margin-bottom: 4px;
  
  
  display: flex;
  align-items: center;
  justify-content: center;
  
  flex-grow: 1;
  flex-basis: 0;
  
  cursor: pointer;
  user-select: none;

  font-size: .9375rem;
  font-weight: 600;
  color: var(--secondary-text);

  border-radius: 4px;

  transition: background-color var(--fast-speed) ease;
  transition-delay: 25ms;
}

.content-reactions-button:hover {
  transition: none;
  transition-delay: 0ms;

  background-color: rgb(242, 242, 242);
}

.content-reactions-button svg {
  width: 18px;
  height: 18px;
}
