.navigation-button {
  cursor: pointer;
  user-select: none;

  display: flex;
  align-items: center;
  
  padding: 8px;

  border-radius: 8px;

  transition-delay: 25ms;
  transition: background-color 150ms ease;

  &:hover {
    transition: none;
    background-color: rgb(228, 230, 233);
  }
  
  
  .navigation-button-icon {
    width: 36px;
    height: 36px;
  
  }
  
  .navigation-button-icon img {
    max-width: 100%;
    max-height: 100%;
  
    border-radius: 50%;
  }
  
  .navigation-button-title {
    margin-left: 12px;
  
    font-weight: 600;
    font-size: .9375rem;
  }
}
