.right-addon-container {
  top: var(--nav-size);
  position: sticky;

  height: 100%;
  width: 100%;
  max-width: 350px;
}

@media only screen and (max-width: 900px) {
  .right-addon-container {
    display: none;
  }
}

.right-addon-content {
  margin-left: 8px;
  margin-top: 28px;
  color: var(--secondary-text);
  font-weight: 600;
  font-size: 1.0625rem;
}

.right-addon-sponsored-section {
  margin-left: 10px;
}

.right-addon-separator {
  height: 1px;
  margin-left: 10px;
  margin-top: 16px;
  background-color: #ced0d4;
}


.right-addon-group-conversations {
  margin-top: 12px;
  margin-left: 10px;
}

.right-addon-create-new-group {
  display: flex;
  align-items: center;

  margin-top: 8px;
  padding-top: 8px;
  padding-bottom: 8px;

  border-radius: 8px;
  cursor: pointer;
  transition: background-color var(--fast-speed) ease;
}

.right-addon-create-new-group:hover {
  background-color: #E4E6E9;
}


.right-addon-create-new-group-icon {
  width: fit-content;
  height: fit-content;
  margin-left: 8px;
  
  cursor: pointer;

  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 20px;

  background-color: rgb(216, 218, 223);
  transition: filter var(--fast-speed) ease;
}

.right-addon-create-new-group-icon svg {
  width: 20px;
  height: 20px;
  
  padding: 8px;
  
  stroke: rgba(216, 218, 223, 0.89);
  stroke-width: 50px;
}




.right-addon-create-new-group-title {
  margin-left: 12px;

  font-size: .9375rem;
  font-weight: 600;
  color: var(--primary-text);

  user-select: none;
}
