.menu-button-container {
  width: fit-content;
  height: fit-content;

  .icon {
    width: fit-content;
    height: fit-content;
    margin: 2px;
    
    cursor: pointer;
    user-select: none;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 20px;

    color: var(--primary-text);
    background-color: var(--secondary-button-background);
    transition: filter var(--fastest-speed) ease, background-color var(--fastest-speed) ease;


    &:hover {
      filter: brightness(0.95);
    }


    svg {
      --button-size: calc(var(--nav-button-size) * 0.5);
      width: var(--button-size);
      height: var(--button-size);
  
      padding: 10px;
  
      fill: var(--button-color);
      transition: fill var(--fastest-speed) ease;
          
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }


  .active {
    background-color: var(--active-button-color);

    svg {
      fill: var(--accent);
    }
  }
  

