.content-origin-container {
  display: flex;
  
  margin-top: 12px;
  margin-bottom: 8px;
  margin-left: 16px;
}

.content-origin-icon {
  width: 40px;
  height: 40px;
}

.content-origin-icon img {
  width: 100%;
  height: 100%;

  border-radius: 50%;
  cursor: pointer;
}

.content-origin-info-container {
  display: flex;
  flex-direction: column;

  margin-left: 8px;
}

.content-origin-info {
  display: flex;
  align-items: center;
}

.content-origin-info-date {
  cursor: pointer;
  
  font-size: .8125rem;
  font-weight: 400;
  color: var(--secondary-text);
}

.content-origin-info-date:hover{
  text-decoration: underline;
}

.content-origin-info-icon svg {
  width: 12px;
  height: 12px;
}

.content-origin-links {
  width: fit-content;

  cursor: pointer;
  
  font-size: .9375rem;
  font-weight: 600;
  color: var(--primary-text);
}

.content-origin-links:hover {
  text-decoration: underline;
}




.content-origin-options-button {
  display: flex;
  
  height: fit-content;
  margin-right: 16px;
  margin-left: auto;
  
  padding: 8px;
  
  cursor: pointer;
  border-radius: 50%;  
  
  transition: background-color var(--fast-speed) ease;
}

.content-origin-options-button:hover {
  background-color: rgb(242,242,242);
}

.content-origin-options-button svg {
  fill: rgb(96, 98, 102);  
  
  width: 20px;
  height: 20px;
}
