.nav-item {
  margin-left: 2px;
  margin-right: 2px;

  display: flex;
  align-items: center;
  justify-content: center;

  font-weight: 600;
  font-size: 0.9375rem;
}

@media only screen and (max-width: 1300px) {
  .hide-nav-item-at-1300 {
    display: none;
  }
}

@media only screen and (max-width: 500px) {
  .hide-nav-item-at-500 {
    display: none;
  }
}

.nav-item a {
  color: inherit;
  text-decoration: none;
}

.nav-item-content-enter {
  filter: opacity(0);
}

.nav-item-content-enter-active {
  filter: opacity(1);
  transition: height var(--fast-speed) ease, filter var(--fast-speed) ease !important;
}

.nav-item-content-exit {
  filter: opacity(1);
}

.nav-item-content-exit-active {
  filter: opacity(0);
  transition: height var(--fast-speed) ease, filter var(--fast-speed) ease !important; 
}





.icon-button {
  width: fit-content;
  height: fit-content;
  margin: 2px;
  
  cursor: pointer;
  user-select: none;

  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 20px;

  color: var(--primary-text);
  background-color: var(--secondary-button-background);
  transition: filter var(--fast-speed) ease;
}

.icon-button-highlighted {
  background-color: var(--primary-deemphasized-button-background);
}

.icon-button-highlighted span {
  color: var(--primary-deemphasized-button-text);
}


.icon-button:hover {
  filter: brightness(0.95);
}

.icon-button svg {
  fill: var(--button-color);
  transition: fill 100ms ease;

  --button-size: calc(var(--nav-button-size) * 0.5);
  width: var(--button-size);
  height: var(--button-size);
  
  padding: 10px;
  
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-button span {
  width: auto;
  height: 35px;
  padding-left: 12px;
  padding-right: 12px;
  
  text-align: center;
  word-break: break-word;
  
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-button-active {
  background-color: var(--active-button-color);
}

.icon-button-active svg {
  fill: var(--accent);
}






.mini-profile {
  width: fit-content;
  height: fit-content;
  
  display: flex;
  align-items: center;
  justify-content: center;

  margin-left: 6px;
  margin-right: 5px;
  border-radius: 20px;
  
  background-color: var(--primary);
  transition: filter var(--fast-speed);
}

.mini-profile img {
  width: 28px;
  height: 28px;

  padding: 4px;
  padding-left: 5px;
  padding-right: 6px;
  border-radius: 30px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.mini-profile span {
  padding-right: 9px;
  padding-bottom: 3px;

  display: flex;
  align-items: center;
  justify-content: center;
}


.mini-profile:hover {
  filter: brightness(0.95);
}



.nav-item-hint {
  z-index: 1;
  /* Hide tooltip behind dropdown on fade out  */
  /* z-index: -1;  */

  pointer-events: none;

  position: absolute;
  margin-top: 95px;
  padding: 8px 13px 9px 13px;

  color: rgb(228, 230, 235);
  font-size: .8125rem;
  font-weight: 400;
  white-space: nowrap;

  border-radius: 8px;

  box-shadow: 0 2px 4px 1px rgba(0, 0, 0, 0.4);
  background-color: var(--tooltip-bg);
  filter: opacity(0);

  transition: filter 200ms ease;
  transition-delay: 20ms;
}

.nav-item-hint-show {
  /* Show tooltip in front of dropdown on fade in  */
  /* z-index: 1; */
  

  filter: opacity(1);
  transition: filter 100ms ease;
  transition-delay: 350ms;
}