
.groups-tab-container {
  display: flex;
  justify-content: center;

  .feed {
    margin-right: auto;

    .recent-activity {
      padding-top: 24px;
      padding-left: 32px;
      margin-bottom: -8px;

      color: var(--secondary-text);
      font-size: .9375rem;
    }
  }


  .groups-menu {
    position: sticky;
    top: var(--nav-size);

    height: calc(100vh - var(--nav-size));
    width: 360px;

    background-color: var(--primary);
    margin-right: auto;
    
    
    .title-block {
      display: flex;
      align-items: center;
      justify-content: flex-start;

      margin-top: 8px;
      margin-left: 16px;
      margin-right: 16px;

      .title {
        margin-right: auto;

        line-height: 1.75rem;
        font-size: 1.5rem;
        font-weight: 700;
        font-family: "Segoe UI", "Segoe UI Historic", Helvetica, Arial, sans-serif;
      }
    }


    .search-container {
      margin-top: 4px;
      margin-left: 16px;
      margin-right: 16px;
    }


    .navigation-block {
      padding-top: 12px;
      padding-left: 12px;
      padding-right: 12px;
    }

    .create-new-group-button {
      height: 36px;

      margin-top: 8px;
      margin-bottom: 16px;
      margin-left: 16px;
      margin-right: 16px;

      text-align: center;
      text-justify: distribute;
      line-height: 36px;

      cursor: pointer;
      user-select: none;

      border-radius: 8px;
      color: var(--primary-deemphasized-button-text);
      background-color: var(--primary-deemphasized-button-background);

      transition: filter var(--fast-speed);

      &:hover {
        filter: brightness(0.9);
      }

    }

  }

  .group-link {
    display: flex;

    margin-left: 12px;
    margin-right: 12px;
    padding: 8px 8px 8px 8px;

    border-radius: 8px;

    cursor: pointer;
    user-select: none;
    transition: background-color var(--fastest-speed) ease;

    &:hover {
      background-color: rgb(228, 230, 233);
    }

    img {
      width: 60px;
      height: 60px;
      border-radius: 8px;
    }

    .info {
      display: flex;
      flex-direction: column;
      justify-content: center;

      margin-left: 12px;

      .group-name {
        font-size: 0.9375rem;
      }

      .additional-info {
        margin-top: 4px;
        font-size: .8125rem;
        font-weight: 400;

        line-height: .8125rem;

        color: var(--secondary-text);
      }
    }
  }


}