.search-menu {
  display: flex;
  align-items: center;
}


.search-menu-fb-icon {
  z-index: 1;

  width: 40px;
  height: 40px;

  margin-left: 20px;

  cursor: pointer;

  transition: filter var(--fast-speed) ease;
}

.search-menu-fb-icon-focused {
  z-index: 0;
  filter: opacity(0);
}


.search-menu-focused-container {  
  width: 360px;
  position: absolute;
  height: 56px;

  border-radius: 10px;
  box-shadow: -5px 5px 10px 2px rgba(0, 0, 0, 0.3);

  background-color: var(--primary);
  
  filter: opacity(0);
  transition: height var(--fast-speed) ease, filter var(--fast-speed) ease;
}

.search-menu-focused-container-focused {
  width: 360px;
  height: 137px;
  filter: opacity(1);
}



.search-menu-search {
  z-index: 1;

  width: 260px;

  margin-left: 10px;
  padding: 9px 8px 8px 8px;

  display: flex;
  align-items: center;
  overflow: hidden;

  border: 0px;
  border-radius: 20px;

  background-color: var(--comment-background);

  transition: width var(--fast-speed) ease, margin-left var(--fast-speed) ease;
  /* transition: width 1000ms ease, margin-left 1000ms ease; */
}


.search-menu-search-focused {
  margin-left: -10px;
  width: 280px;
}


.search-menu-icon {
  margin-top: 2px;
  margin-left: 4px;

  transition: margin-left var(--fast-speed) ease;
  /* transition: margin-left 1000ms ease; */
}

.search-menu-icon svg {
  color: var(--secondary-icon);

  width: 16px;
  height: 16px;

  transition: width var(--fast-speed) ease, height var(--fast-speed) ease, filter var(--fast-speed) ease, margin-top var(--fast-speed) ease;
  /* transition: width 1000ms ease, height 1000ms ease, filter 1000ms ease, margin-top 1000ms ease; */
}

.search-menu-icon-focused {
  margin-left: -16px;
}

.search-menu-icon-focused svg {
  filter: opacity(0);
}



.search-menu-input {
  height: 100%;
  width: 100%;

  padding-left: 1ch;
  margin-top: -3px;

  font-family: "Segoe UI Historic", "Segoe UI", Helvetica, Arial, sans-serif;
  font-size: 0.9375rem;
  font-weight: 400;
  color: black;

  background-color: transparent;
  border: 0px;
}

.search-menu-input:focus {
  outline: none;
}



@media only screen and (max-width: 1300px) {
  .search-menu-search {
    width: 180px;
  }

  .search-menu-search-focused {
    width: 200px;
  }
  
  .search-menu-focused-container {
    width: 275px;
  }

  .search-menu-focused-container-focused {
    width: 275px;
  }
}

@media only screen and (max-width: 1100px) {
  .search-menu-search {
    width: 80px;
  }

  .search-menu-search-focused {
    width: 200px;
  }

  .search-menu-focused-container {
    width: 180px;
    transition: width 0ms ease var(--normal-speed), height var(--fast-speed) ease, filter var(--fast-speed) ease;
  }

  .search-menu-focused-container-focused {
    width: 275px;
    transition: width 0ms ease 0ms, height var(--fast-speed) ease, filter var(--fast-speed) ease;
  }
}

