body {
  margin: 0;
  padding: 0;
  background-color: var(--bg);

  font-family: "Segoe UI Semibold", "Segoe UI", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  overflow-y: scroll;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}


:root {
  font-size: 16px;

  /* colors */
  --bg: #f0f2f5;

  --primary:  #ffffff;
  --primary-highlight: #f2f2f2;
  --accent: #1876f2;

  --primary-text: #050505;
  --secondary-text: #65676B;
  --primary-deemphasized-button-text: #1877F2;
  
  --secondary-icon: #65676B;
  --hover: rgba(0, 0, 0, 0.05);

  --comment-background: #F0F2F5;

  --button-color: #050505;
  --button-lighter-color: #5f5f5f;
  --active-button-color: #e7f3ff;
  --secondary-button-background: #E4E6EB;
  --background-deemphasized: #F0F2F5;
  --primary-deemphasized-button-background: #E7F3FF;
  
  --tooltip-bg: rgb(57, 57, 58);

  --segoeui-semibold: "Segoe UI Semibold", "Segoe UI", Helvetica, Arial, sans-serif;
  --segoeui: "Segoe UI", "Segoe UI Semibold", Helvetica, Arial, sans-serif;



  /* other */
  --nav-size: 56px;
  --nav-button-size: 40px;
  --border: 1px solid #474a4d;
  --border-radius: 8px;

  --fastest-speed: 100ms;
  --fast-speed: 150ms; 
  --normal-speed: 250ms;
  /* --fast-speed: 15000ms; 
  --normal-speed: 15000ms; */
}

::selection {
  color: white;
  background: #3390ff;
}
