.slide-left-enter {
  position: absolute;
  transform: translateX(-110%);
}

.slide-left-enter-active {
  transform: translateX(0%);
  transition: transform var(--fast-speed) ease;
}

.slide-left-exit {
  position: absolute;
}

.slide-left-exit-active {
  transform: translateX(-110%);
  transition: transform var(--fast-speed) ease;
}



.slide-right-enter {
  position: absolute;
  transform: translateX(110%);
}

.slide-right-enter-active {
  transform: translateX(0%);
  transition: transform var(--fast-speed) ease;
}

.slide-right-exit {
  position: absolute;
}

.slide-right-exit-active {
  transform: translateX(110%);
  transition: transform var(--fast-speed) ease;
}
