.navbar {
  top: 0px;
  position: sticky;

  height: var(--nav-size);

  z-index: 10;
}

.navbar-layer-1 {
  position: absolute;
  pointer-events: none;

  height: var(--nav-size);
  width: 100%;
  
  display: flex;
  justify-content: center;
  align-items: stretch;

  background-color: transparent;
}

.navbar-layer-2 {
  height: var(--nav-size);
  
  display: flex;
  justify-content: space-between;
  align-items: stretch;

  background-color: var(--primary);
  border-bottom: 1px solid rgba(0, 0, 0, 0.13);
  box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.05);
}



