.comments-section-container {
  margin-top: 24px;
}

.comment-container {
  margin-left: 16px;
  margin-right: 42px;
  display: flex;
}

.comment-user-icon {
  width: 32px;
  min-width: 32px;
  height: 32px;

  margin-top: 2px;
  
  cursor: pointer;
}

.comment-user-icon img {
  width: 100%;
  height: 100%;
  
  border-radius: 50%;
}




.comment-body {
  margin-left: 6px;
}

.comment-message {
  width: fit-content;

  padding: 6px;
  padding-left: 12px;
  padding-right: 12px;

  font-family: var(--segoeui);
  font-size: .9375rem;
  font-weight: 400;
  color: var(--primary-text);

  background-color: var(--comment-background);
  border-radius: 16px;
}

.comment-user-name {
  user-select: none;
  cursor: pointer;

  color:var(--primary-text);
  font-weight: 600;
  font-size: .8125rem;
}

.comment-user-name:hover {
  text-decoration: underline;
}


.comment-reaction-container {
  display: flex;
  align-items: center;

  margin-left: 12px;
  margin-top: 2px;
  margin-bottom: 8px;
}

.comment-reaction-button {
  margin-right: 16px;
  cursor: pointer;
  
  font-weight: 700;
  font-size: 0.75rem;
  color: var(--secondary-text)
}

.comment-reaction-button:hover {
  text-decoration: underline;
}


.comment-reaction-info {
  margin-right: 12px;
  cursor: pointer;

  font-weight: 500;
  font-size: 0.75rem;
  color: var(--secondary-text)
}

.comment-reaction-info:hover {
  text-decoration: underline;
}