.navigation-menu-container {
  top: var(--nav-size);
  position: sticky;

  height: 100%;
  width: 100%;
  max-width: 350px;
}

@media only screen and (max-width: 1300px) {
  .navigation-menu-container {
    max-width: 250px;
  }
}

@media only screen and (max-width: 1100px) {
  .navigation-menu-container {
    display: none;
  }
}


.navigation-menu-content {
  max-height: calc(100vh - var(--nav-size));
  margin-right: 20px;

  overflow-y: auto;
  overscroll-behavior: contain;

  color: transparent;
  transition: color var(--normal-speed) ease;
}

.navigation-menu-content:hover{
  color: rgb(188, 192, 196);
  transition: color var(--fast-speed) ease;
}

.navigation-menu-content::-webkit-scrollbar {
	width: 16px;
}


.navigation-menu-content::-webkit-scrollbar-thumb {
	border: 4px solid transparent;
	border-radius: 8px;
	box-shadow: inset 0 0 0 10px;
}



.navigation-menu-buttons-list {
  margin-left: 8px;
  margin-top: 16px;

  color: var(--primary-text);
}


.navigation-button-svg-icon {
  width: 36px;
  height: 36px;

  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 20px;

  
  background-color: rgb(218, 220, 223);
}

.navigation-button-svg-icon svg {
  max-width: 20px;
  max-height: 20px;
  
  transition: transform var(--fast-speed) ease;
  
  border-radius: 50%;
}




.navigation-menu-separator {
  height: 1px;
  margin-left: 16px;
  margin-top: 16px;
  background-color: #ced0d4;
}





.navigation-menu-shortcuts {
  margin-top: 12px;
  margin-bottom: 12px;
  margin-left: 8px;

  color: var(--primary-text);
}

.navigation-menu-shortcuts-title {
  margin-left: 8px;
  margin-bottom: 8px;

  color: var(--secondary-text);
  font-weight: 600;
  font-size: 1.0765rem;
}