.friends-content-place-holder {
  height: 100%;
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.friends-content-place-holder img {
  width: 120px;
  height: 120px;

  margin-bottom: 16px;
}

.friends-content-place-holder-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--secondary-text);
  font-family: var(--segoeui);
}



.friends-content-place-holder-description {
  font-size: 1.0625rem;
  font-weight: 400;
  color: var(--secondary-text);
  font-family: var(--segoeui);
}